@import url(https://fonts.googleapis.com/css2?family=Rubik&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@600&display=swap);
* {
  box-sizing: border-box;
  padding : 0;
  margin  : 0;  
}

a {
  text-decoration: none;
  color: lightgray;
}

body { 
   font-family: 'Rubik', sans-serif;
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
   background-color: #36435e;
   color: white;
   max-width: 1100px;
   margin: 0 auto;   
}
.App{padding:20px;text-align:left}.won-card{background-color:#4da375}.lost-card{background-color:#a34d5d}

.MatchDetailCard{display:grid;grid-template-columns:1fr 1fr;padding:10px;margin-bottom:20px}.MatchDetailCard .match-date,.MatchDetailCard .match-venue,.MatchDetailCard .match-result{margin-top:10px}.MatchDetailCard .additional-detail{text-align:right}.MatchDetailCard .additional-detail>p{margin-bottom:20px}

.MatchSmallCard{padding:10px;margin-bottom:60px}.MatchSmallCard .match-result{margin-top:10px}

.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}.TeamPage .home-link{align-self:start}.TeamPage .team-name-section{grid-column:span 3;display:grid;grid-template-rows:1fr 1fr}.TeamPage .team-name{font-size:4rem;margin-bottom:20px;justify-self:start;align-self:end}.TeamPage .win-loss-section{justify-self:center;padding:40px;text-align:center}.TeamPage .match-detail-section{grid-column:span 4}.TeamPage .more-link{align-self:center;justify-self:center;font-size:1.5rem}

.MatchPage{display:grid;grid-template-rows:40px 1fr;grid-template-columns:1fr 5fr;grid-column-gap:20px}.MatchPage .home-link{align-self:center;grid-column:span 2}.MatchPage .year-selector{justify-self:end;padding:20px}.MatchPage .page-heading{margin:20px auto}.MatchPage .no-matches{color:wheat;padding:30px}

.YearSelector{text-align:right;list-style-type:none}.YearSelector li{margin-top:10px;margin-bottom:10px}

.HomePage .app-name{font-size:3rem;margin:30px auto}.HomePage .team-grid{display:grid;grid-template-columns:1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}

.TeamTile{height:170px;text-align:center;border:1px solid white;padding:20px;background-color:sienna}

